import React, { useEffect, useState } from "react";
import "./style.scss";

export const PopupAttention = ({ onClose, message }) => {
  const [classes, setClasses] = useState("popup_attention");

  useEffect(() => {
    const timerFadein = setTimeout(() => {
      setClasses("popup_attention popup_attention-fadein");
    }, 100);

    const timerFadeout = setTimeout(() => {
      setClasses("popup_attention");
    }, 2000);
    const timerClose = setTimeout(onClose, 2500);

    // componentWillUnmount
    return () => {
      clearTimeout(timerFadein);
      clearTimeout(timerFadeout);
      clearTimeout(timerClose);
    };
  }, [message]);

  return <div className={classes}>{message}</div>;
};
