export const preText = string => {
  let zeroOctet = "https://www.facebook.com";
  let firstOctet = "";
  let secondOctet = "";

  if (!string) {
    return "";
  }

  if (string.indexOf("photos") !== -1) {
    firstOctet = string.split("/a.")[0]?.replace("photos", "posts");
    secondOctet = string.split("/a.")[1]?.split("/")[1];
    return `${firstOctet}/${secondOctet}`;
  }

  if (string.indexOf("videos") !== -1) {
    firstOctet = string?.split("?")[0];
    return firstOctet?.replace("videos", "posts");
  }

  if (string.indexOf("posts") !== -1) {
    firstOctet = string?.split("?")[0];
    return firstOctet;
  }

  if (string.indexOf("video") !== -1) {
    firstOctet = string.split("/a.")[0]?.replace("video", "posts");
    secondOctet = string.split("/a.")[1]?.split("/")[1];
    return `${firstOctet}/${secondOctet}`;
  }

  if (string.indexOf("photo") !== -1) {
    firstOctet = string.split("/a.")[0]?.replace("photo", "posts");
    secondOctet = string.split("/a.")[1]?.split("/")[1];
    return `${firstOctet}/${secondOctet}`;
  }

  if (string.indexOf("permalink.php") !== -1) {
    firstOctet = string?.split("id=")[2]?.split("&")[0];
    secondOctet = string?.split("id=")[1].split("&")[0];
    return `${zeroOctet}/${firstOctet}/posts/${secondOctet}`;
  }

  return "";
};
