import React, { useEffect, useState } from "react";
import { PopupAttention } from "../PopUp";
import { preText } from "./utils";
import "./style.scss";

export const Convert = () => {
  const [isShow, setIsShow] = useState(false);
  const [isMessage, setIsMessage] = useState("");

  const [isText, setIsText] = useState("");
  const [isConvertText, setIsConvertText] = useState("");

  useEffect(() => {
    isText && setIsConvertText(preText(isText));
  }, [isText]);

  useEffect(() => {
    if (isConvertText) {
      window.navigator.clipboard.writeText(isConvertText);
      setIsMessage("Ссылка скопирована в буфер");
      setIsShow(true);
    }
  }, [isConvertText]);

  const handleChange = event => {
    setIsShow(false);
    setIsText(event.currentTarget.value);
  };

  return (
    <>
      <div className="convert">
        <div className="convert-input">
          <div>полная ссылка</div>
          <input
            placeholder={"Вставь сюда полную ссылку"}
            type="text"
            onChange={handleChange}
            value={isText}
          />
        </div>

        <div className="convert-input">
          <div>{`короткая ссылка ${isConvertText &&
            "(нажми и я откроюсь в новой вкладке)"}`}</div>
          <div className="convert-label">
            {isConvertText && (
              <span onClick={() => window.open(isConvertText)}>
                {isConvertText}
              </span>
            )}
            {!isConvertText && "В этом месте будет готовая ссылка"}
          </div>
        </div>
        <div className="convert-input-custom">
          <div
            className="convert-input-custom-btn"
            onClick={() => {
              setIsText("");
              setIsShow(true);
              setIsConvertText("");
              setIsMessage("Поля очищены");
            }}
          >
            очистить поля
          </div>
          <div
            className="convert-input-custom-btn"
            onClick={() => {
              setIsMessage("Буффер очищен");
              setIsShow(true);
              window.navigator.clipboard.writeText("");
            }}
          >
            очистить буфер
          </div>
        </div>
      </div>
      <div className="footer">
        <div>
          После вставки полной ссылки, готовая ссыкла копируется в буффер
          автоматически
        </div>
        <div>
          конвертируемые ссылки:{" "}
          <span>photos\photo, videos\video, posts, permalink.php</span>
        </div>
        <a href="tg://resolve?domain=codernd">
          есть вопросы? <br />
          телега: @codernd
        </a>
      </div>
      {isShow && <PopupAttention onClose={setIsShow} message={isMessage} />}
    </>
  );
};
